
.container, .translatedContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 45vh; 
    width: 100%;
    border: solid var(--text-color) 1px;
    color: var(--text-color);
    background-color: var(--back-color);
    border-radius: 2vh;
    animation-name: translation-animation;
    animation-duration: 1.5s;
}

.translatedContainer{
    transform: translateY(9vh);
}

@keyframes translation-animation{
    0%{
        transform: translateY(50vh);
        opacity: 0.3
    };
}


.header{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15%;
    width: 100%;
    border-bottom: solid var(--text-color) 1px;
}

.footer{
    height: 20%;
    width: 100%;
    border-top: solid var(--text-color) 1px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.imageStyle{
    border: solid var(--text-color) 1px;
    border-radius: 1vh;
}

a:link, a:visited{
    font-size: 2.5vh;
    text-decoration: none;
    color: var(--text-color);
    font-weight: lighter;
}