:root {
  --text-color: lightblue;
  --back-color: rgba(45, 40, 40);
  --transition-duration: 0.3s;
  --text-color-transparent:rgba(173,216,230,0.5)
}

body{
  background-color: var(--back-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: "Gill Sans", sans-serif;
  transition-duration: var(--transition-duration);
  transition-property: color, background-color, border;
  
}

.header{
  width: 100%;
  height: 8vh;
  display:grid;
  grid-template-columns: 6fr 3fr;
  justify-content: space-around;
  align-items: center;
  border-bottom: var(--text-color) solid 0px; 
  transition-duration: var(--transition-duration);
  transition-property: color, background-color, border;
  font-weight: lighter;
}

.second-part-header{
 display: flex;
 justify-content: center;
 align-items: center;
}

.my-button, .menu-button{
  color: var(--text-color);
  padding-bottom: 0.7vh;
  font-size: 2.6vh;
  transition-duration: var(--transition-duration);
  transition-property: color;
  margin-left: 1.3vw;
}

.my-button:hover, .menu-button:hover{
  cursor: pointer;
}

.menu-button{
  width: 5.1vh;
  margin-left: 7vw;
}

.app{
  width: 100vw;
  height: 92vh;
}

.vertical-bar{
  position: relative;
  background-color: var(--text-color);
  width: 1px;
  height: 2.5vh;
  left: 0.65vw;
  top: -0.9vh;
}

.circle{
  pointer-events: none;
  position: relative;
  height: 0.8vh;
  width: 0.8vh;
  border: solid var(--text-color) 1px;
  border-radius: 4vh;
  top: 1vh;
  margin-left: auto;
  margin-right: auto ;
  opacity: 0;
  transition-duration: 0.5s
}

.active{
  opacity:1
}

.fancyShape{
  position: absolute;
  left: 73vw;
  top: 69vh; 
  height: 20vh;
  width: 20vh;
  border-radius: 32% 59% 13% 81% / 58% 61% 34% 35% ;
  border: solid var(--text-color) 0px;
  animation-name: fancy-move, appearance, back-animation, opacity-change;
  animation-duration: 10s, 9s, 9s, 4s;
  animation-delay: 0s, 0s, 0s, 5s;
  animation-iteration-count: infinite, 1, infinite, infinite;
  animation-direction: normal, normal, alternate, alternate;
  animation-timing-function: linear;
  display:flex;
  justify-content: center;
  align-items: center;
  color: rgb(240, 240, 240);
  font-size: 1.7vh;
  transition-duration: 1.2s;
  background: linear-gradient(to right,red, blue, var(--back-color));
  background-size: 910%;
  transition-duration: 2s;
}

.fancyShape:hover{
  transform: scale(3) rotate(80deg) translate(2vh, -1vw);
  left: 76vw;
  top: 72vh; 
  color: transparent;
  border-style: dotted;
  opacity:0.8
}

.activeShape{
  transform: scale(3) rotate(80deg) translate(2vh, -1vw);
  left: 76vw;
  top: 72vh; 
  color: transparent;
  border-style: dotted;
  opacity: 1;
  z-index: -1;
}

@keyframes fancy-move{
  0%{
      border-radius: 32% 59% 13% 81% / 58% 61% 34% 35% 
  }  
  33%{
    border-radius: 61% 31% 26% 63% / 60% 39% 50% 32%  ;
  }
  66%{
      border-radius: 72% 21% 66% 28% / 18% 20% 70% 76%   ;
    }
  100%{
      border-radius: 32% 59% 13% 81% / 58% 61% 34% 35% 
  } 
}

@keyframes back-animation{
  100%{
    background-position: 90%, 0%
  }
}

@keyframes opacity-change{
  100%{
    opacity:0.7
  }
}

@keyframes appearance{
  0%{
      opacity: 0
  }
  50%{
      opacity: 0
  }
}

.unselectable{
  -moz-user-select: none; 
  -webkit-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
}

