.CVContainer{
    width: 100vw;
    position: relative;
}



.circle{
    border: solid black 0px;
    position: relative;
    height: 50vh;
    width: 50vh;
    border-radius: 50vh;
    animation-name: rotation, translation;
    animation-duration: 100s, 100s;
    animation-iteration-count: infinite, infinite;
    animation-timing-function: ease-in-out, ease-in-out;
    animation-direction: normal, alternate;
    transition-duration: 5s;
    transition-property: left;
}

@keyframes rotation{
    100%{
        transform: rotate(360deg)
    }
}

@keyframes translation{
}

.container{
    border: solid var(--text-color) 2px;
    resize: both;
    overflow: auto;
    background-attachment: scroll;
    margin-left: 10vw;
    height: 55vh;
    border-radius: 1.3vh;
    width:50vw;
    animation-name: translation-animation;
    animation-duration: 1s;
}

@keyframes translation-animation{
    0%{
        transform: translate(-10vw, 15vh) rotate(60deg) scale(0.1);
        opacity:0.5
    }
}

.containerRight{
    position: absolute;
    border: solid var(--text-color) 2px;
    margin-left: 10vw;
    padding: 1vw;
    border-radius: 1.3vh;
    width: 10vw;
    right: 10vw;
    top: 10vh;
    animation-name: translation-animation-right;
    animation-duration: 1.5s;
}

@keyframes translation-animation-right{
    0%{
        transform: translate(20vw, 30vh) rotate(430deg);
        opacity:0.5
    }
}


.CVImage{
    width:50vw; /* you can use % */
    height: auto; 
    margin-right: auto;
    margin-left: auto;   
}

@media (max-width: 420px) {
    .CVImage{
        width:90vw; /* you can use % */ 
    }
    .container{
        height: auto;
    }
  }

  @media (max-width: 720px) {
    .CVImage{
        width:80vw; /* you can use % */ 
