.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column; 
    align-items: center;
    z-index: 2;
    overflow-y: scroll;
}

.textContainer{
    width: 80%;
}

.subLine{
    width: 100%;
    height: 1px;
    background-color: var(--text-color)
}

.publicationsList{
    display: grid;
    grid-template-columns: 1fr 40fr;
    margin-left: auto;
    margin-right: auto;
    padding: 1vw;
}

h1{
    margin-top: 5vh;
    color: var(--text-color)
}

.publicationsText{
    font-size: 2.6vh;
}

p .link{
    text-decoration: underline;
}

