.container{
    color: var(--text-Color);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: 200;    
}

p{
    color: var(--text-color);
    padding: 0;
    margin:0;
    font-size: 9.5vh;
    transition-duration: var(--transition-duration);
    transition-property: color;
}

.unselectable{
  -moz-user-select: none; 
  -webkit-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
}

.text1{
    animation-name: appearance;
    animation-duration: 2s;
    transition-duration: 2s;
    transition-property: transform;
    font-weight: 200;
}

.text2{
    animation-name: appearance;
    animation-duration: 4s;
    transition-duration: 2s;
    transition-delay: 0.2s;
    transition-property: transform;
    font-weight: light;
}

.changingText{
    animation-name:  appearance;
    animation-duration: 6s;
    transition-duration: 2s;
    transition-delay: 0.4s;
    transition-property: transform;
    font-weight: light;
}

.activeAnimText{
  animation-name: text-change;
  animation-duration: 3s
}

.centralTextActive{
    transform: translateY(-15vh);
}

@keyframes text-change{
    0%{
        opacity: 0
    }
    70%{
        opacity: 1
    }
}

@keyframes appearance{
    0%{
        opacity: 0
    }
    50%{
        opacity: 0
    }
}
  .textRotate{
      position: absolute;
      left: -2vw;
      top: 45vh;
      transform: rotate(-90deg);
      color: var(--text-color);
      font-size: 5vw;
      opacity: 0.01;
      animation-name: size-modification-closing, appearance-cycle;
      animation-duration: 1.4s, 5s;
      animation-direction: normal, alternate;
      animation-iteration-count: 1, infinite;
      transition-duration: 2s; 
      transition-property: transform, height, width;
      font-weight: lighter;
  }

  .active{
    overflow: scroll;
    transform: rotate(0deg);
    height: 40vh;
    width: 40vw;
    border: solid 2px var(--back-color);
    border-radius: 5px 5px 3vh 3vh;
    animation-name : none;
    color : var(--back-color);
    opacity: 1;
    left: 5vh;
    top: 35vh;
    background-color: var(--text-color);
    font-weight: 300;
    animation-name: size-modification;
    animation-duration: 1.4s;
    animation-iteration-count: 1;
  }

  @keyframes size-modification-closing{
    0%{
      height: 40vh;
      width: 40vw;
    }
}

  @keyframes size-modification{
      0%{
        height: 10vh;
        width: 30vw;
      }
  }

  .subText{
      pointer-events:none;
      position:relative;
      left: 20%;
      width: 76%;
      height: 60%;
      font-size: 2vh;
      font-weight: lighter;
      margin-top: 5%;
      text-align: justify;
      display:none;
      transition-duration: 1s;
  }

  .activeText{
      display: block;
  }

  @keyframes appearance-cycle{
    100%{
      opacity: 0.5
    }
  }

  
  @keyframes fancy-move{
    0%{
        border-radius: 32% 59% 13% 81% / 58% 61% 34% 35% 
    }  
    33%{
      border-radius: 61% 31% 26% 63% / 60% 39% 50% 32%  ;
    }
    66%{
        border-radius: 72% 21% 66% 28% / 18% 20% 70% 76%   ;
      }
    100%{
        border-radius: 32% 59% 13% 81% / 58% 61% 34% 35% 
    } 
  }

  @keyframes back-animation{
    100%{
      background-position: 90%, 0%
    }
  }

  @keyframes opacity-change{
    100%{
      opacity:0.7
    }
  }

