.container{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid{
    padding: 5px;
    height: 80%;
    width: 80%;
    border: solid var(--text-color) 3px;
    border-radius: 2vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
    overflow-y: scroll;
    background-color: var(--text-color-transparent);
    z-index:2
}

