.conferenceDetails{
    width: 80%;
    height: 30vh;
    border: solid var(--text-color) 1px; 
    margin-left: auto;
    margin-right: auto;
    margin-top : 3vh;
    border-radius: 0.5vh 0.5vh 3vh 3vh; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    animation-name: appearance;
    animation-duration: 1s;
    background-color: var(--back-color);
    z-index: 2;
    color: var(--text-color);
    
}

.header{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85%;
    height: 25%;
    border-bottom: solid var(--back-color) 1px; 
}

.textContainer{
    margin-top: 1vh;
    width: 75%;
    height: 100%;
}

.conferenceTitle{
    font-size: 3.3vh;
    animation-name: text-appearance;
    animation-duration: 1.5s;
    color: var(--text-color);
}

.conferenceText{
    font-size: 2.6vh;
    text-align: justify;
    animation-name: text-appearance;
    animation-duration: 2s;
    color: var(--text-color);
}

@keyframes appearance {
    0%{
        left: 0;
        width: 20%;
        height: 10vh;
    }
    
}

@keyframes text-appearance {
    0%{   
        opacity: 0;
    }
    50%{   
        opacity: 0;
    }   
}

.myLogo{
    opacity: 1;
    filter: brightness(155%);
    animation-name: logoAppearance;
    animation-duration: 2.3s
}

@keyframes logoAppearance{
    0%{
        opacity: 0
    }
    60%{
        opacity:0
    }
}

.logoContainer{
    position: relative;
    left: 80%;
    height: 5vh;
    display: flex;
    align-items: center;
    opacity: 1;
}